/*------------------------------------------------------------------

  Base Styles

-------------------------------------------------------------------*/
html, body {
  height: 100%; }

/*
 * Font
 */
body {
  font: 14px/1.42857143 "Open Sans", sans-serif;
  color: #fff;
  letter-spacing: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0a0a0a;
  background: rgba(10, 10, 10, 0.9); }

/*
 * Headings
 */
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Josefin Sans", sans-serif;
  color: #fff; }

h1, .h1 {
  font-size: 5rem;
  margin-top: 6rem;
  margin-bottom: 4rem; }
  h1 .btn, .h1 .btn {
    margin-top: 7px; }
  h1 .btn-lg, .h1 .btn-lg {
    margin-top: 0; }

h2, .h2 {
  margin-top: 4rem;
  margin-bottom: 2.5rem; }

p {
  margin-bottom: 1rem;
  margin-top: 1rem; }

.label {
  font-weight: normal; }

iframe {
  border: 0; }

.fa {
  transform: none; }

/*
 * Links
 */
a {
  color: inherit;
  text-decoration: underline;
  transition: .3s color ease; }

a:hover,
a:focus {
  color: #C9572A; }

/*
 * Images max width
 */
img {
  max-width: 100%; }

/*
 * Social List
 */
.social-list {
  display: block;
  margin: 1rem 0;
  clear: both; }
  .social-list:after, .social-list:before {
    content: '';
    display: table;
    clear: both; }

/*
 * Tags
 */
.tags {
  margin: 1rem 0;
  display: block;
  clear: both;
  color: rgba(255, 255, 255, 0.5);
  font-size: 1.2rem; }
  .tags a {
    text-decoration: none; }

/*
 * Date
 */
.date {
  margin-top: 8px;
  color: rgba(255, 255, 255, 0.5);
  font-size: 1.2rem; }
  .date i {
    margin-right: 3px; }

/*
 * Meta
 */
.meta {
  display: block;
  clear: both;
  font-size: 1.2rem;
  margin: 1rem 0; }
  .meta .item {
    display: inline-block;
    margin-right: 2rem; }
  .meta .meta-icon {
    font-size: 1.5rem;
    margin-right: 3px;
    vertical-align: text-bottom; }
  .meta a {
    font-weight: bold;
    text-decoration: none; }

/*
 * Description
 */
.description {
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 2rem; }

/*
 * Content
 */
.content-wrap {
  max-width: 1400px;
  margin: 0 auto;
  background: #0a0a0a;
  padding: 0.1px;
  overflow: hidden; }
  .content-wrap .youplay-content {
    margin-top: 6rem;
    margin-bottom: 6rem; }
  .content-wrap.full, .content-wrap.wide {
    max-width: none; }
  @media (max-width: 767px), (min-width: 767px) and (max-width: 991px) {
    .content-wrap .youplay-content .side-block:first-child {
      margin-top: 6rem; }
    .content-wrap .youplay-content .side-block:last-child {
      margin-bottom: 6rem; } }

.youplay-section {
  margin-bottom: 6rem; }

/*
 * Google Maps
 */
.youplay-gmaps {
  height: 450px;
  width: 100%; }

